<template>
  <div class="container">
    <!-- <button style="margin-top:-40rpx" open-type="getPhoneNumber" @getphonenumber="getPhoneNumber">获取手机号</button>
    <button style="margin-top:-40rpx" @click="Pay">支付</button> -->
    <!-- <div class="in"> -->
      <!-- <div>
    <input style="background:white;margin-top:50rpx;text-align:center" type="number" placeholder="请输入手机号" v-model="mobile"/>
    <button style="margin-top:40rpx" @click="getCustomerid()">确认</button>
    </div> -->
    <img src="https://lg-2aw9z8qs-1257131072.cos.ap-shanghai.myqcloud.com/people_default.png">
    <!-- <button style="margin-top:-10vh" open-type="getUserInfo" @getuserinfo="bindGetUserInfo" >立即前往绑卡</button> -->
    <button  open-type="getPhoneNumber" @getphonenumber="getPhoneNumber">立即前往绑卡</button>
    <!-- <button open-type="getUserInfo" style="margin-top:80rpx" @getuserinfo="bindGetUserInfo" >立即申请会员卡</button>
    <a @click="toBind" style="margin-top:120rpx" hover-class="none">已有会员卡，立即前往绑定</a> -->
    <!-- </div> -->
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      mobile: "",
      customer_id: ""
    };
  },
  methods: {
    ...mapActions(["member_bind", "getForward"]),
    Pay() {
      wx.requestPayment({
        timeStamp: "",
        nonceStr: "",
        package: "",
        signType: "MD5",
        paySign: "",
        success: function(res) {},
        fail: function(res) {}
      });
    },
    async getPhoneNumber(e) {
      if (e.mp.detail.encryptedData) {
      var store = this.$store.state;
      const res = await this.getForward([
        {
          url: "/wechatcallbackserver/api/mini/decrypt",
          mutations: "setDdata",
          server: 1
        },
        {
          ai: store.ai,
          ed: e.mp.detail.encryptedData,
          iv: e.mp.detail.iv,
          sk: store.sessionKey
        }
      ]);
        this.mobile = JSON.parse(res.data).purePhoneNumber;
        this.getCustomerid();
        if (store.login_type == 1) {
            wx.showModal({
              title:"温馨提示",
              content:"请通过扫描商家二维码使用该小程序！",
              showCancel: false
            })
          } else if (store.login_type == 2) {
            var url = "../bindcard/main";
            wx.navigateTo({ url });
          }
      }else{
         console.log("用户按了拒绝按钮");
      }
    },
    toRegister() {
      var url = "../register/main";
      wx.navigateTo({ url });
    },
    toBind() {
      if (this.customer_id == "") {
        wx.showToast({
          title: "请先输入手机号！",
          icon: "none",
          duration: 2000
        });
      } else {
        let store = this.$store.state;
        if (store.login_type == 1) {
          var url = "../chooseKtv/main?type=1";
          wx.navigateTo({ url });
        } else if (store.login_type == 2) {
          var url = "../bindcard/main";
          wx.navigateTo({ url });
        }
      }
    },
    async bindGetUserInfo(e) {
      console.log(e.mp.detail);
      var store = this.$store.state;
      const res = await this.getForward([
        {
          url: "/wechatcallbackserver/api/mini/decrypt",
          mutations: "setDdate",
          server: 1
        },
        {
          ai: store.ai,
          ed: e.mp.detail.encryptedData,
          iv: e.mp.detail.iv,
          sk: store.sessionKey
        }
      ]);
      console.log(JSON.parse(res.data));
      if (this.customer_id == "") {
        wx.showToast({
          title: "请先输入手机号！",
          icon: "none",
          duration: 2000
        });
      } else {
        if (e.mp.detail.rawData) {
          if (store.login_type == 1) {
            var url = "../chooseKtv/main?type=2";
            wx.navigateTo({ url });
          } else if (store.login_type == 2) {
            var url = "../bindcard/main";
            wx.navigateTo({ url });
          }
        } else {
          //用户按了拒绝按钮
          console.log("用户按了拒绝按钮");
        }
      }
    },
    async getCustomerid() {
      this.$store.state.mobile = this.mobile;
      const res = await this.member_bind({
        openid: this.$store.state.openid,
        mobile: this.mobile,
        type: 2
      });
      if (res.ret == 0) {
        this.customer_id = res.data.customer_id;
        // this.$store.state.customer_id = res.data.customer_id;
        // console.log("customer_id:"+this.$store.state.customer_id)
      } else {
        wx.showToast({
          title: "customer_id获取失败！",
          icon: "none",
          duration: 1000
        });
      }
    }
  },
  mounted() {
    wx.setEnableDebug({
      enableDebug: true
    });
  }
};
</script>

<style scoped lang="stylus">
.container {
  background: bg_color;
  width: 100%;
  height: 100vh;
}

.in {
  border-radius: 30px;
  background: white;
  width: 65%;
  height: 50vh;
  align-content: center;
}

button {
  position relative
  top -10vh
  background: primary_color;
  color: white;
  font-size: 35rpx;
  text-align: center;
  width: 45vw;
}

img {
  height: 500rpx;
  width: 470rpx;
}

a {
  text-align: center;
  margin-top: 170rpx;
  font-size: 30rpx;
  text-decoration: underline;
}
</style>



